import * as React from 'react';
import { Helmet } from 'react-helmet';
import { Link } from 'gatsby';
import { useQueryParam, StringParam } from 'use-query-params';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import GlobalStyles from '@mui/material/GlobalStyles';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Logo from '../../images/logo-fff.svg';
import Chara01 from '../../images/character-01-180x492.png';

import membersData from '../../datas/members';

const fontFamily = [
  'Noto Sans JP',
  'Helvetica Neue',
  'Arial',
  'Hiragino Kaku Gothic ProN',
  'Hiragino Sans',
  'Meiryo',
  'sans-serif'
]
  .map(f => `"${f}"`)
  .join(',');

const orange = '#ef8468';

const theme = createTheme({
  palette: {
    primary: {
      main: orange
    }
  },
  typography: {
    fontFamily,
    h3: {
      color: '#403939',
      fontFamily,
      fontSize: 24,
      lineHeight: '36px',
      fontWeight: 'bold'
    },
    body1: {
      fontFamily,
      fontSize: 16,
      lineHeight: '30px'
    }
  }
});

theme.typography.h2 = {
  color: orange,
  fontFamily,
  fontSize: 28,
  lineHeight: '42px',
  fontWeight: 'bold',
  [theme.breakpoints.up('sm')]: {
    fontSize: 32
  }
};

const CustomTable = ({ datas }) => (
  <TableContainer>
    <Table>
      <TableBody>
        {datas.map(({ key, value, link }, i) => (
          <TableRow key={i}>
            <TableCell
              sx={{
                p: 0
              }}
            >
              <Typography fontWeight='bold'>{key}</Typography>
            </TableCell>
            <TableCell>
              {link ? (
                <a href={link}>
                  <Typography color='primary'>{value}</Typography>
                </a>
              ) : (
                <Typography>{value}</Typography>
              )}
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  </TableContainer>
);

const MembersIndex = () => {
  const [id] = useQueryParam('id', StringParam);

  const memberData = membersData(id);

  // @TODO
  if (!memberData) return <Typography variant='body2'>Loading...</Typography>;

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles
        styles={{
          '*, html, body': { margin: 0, padding: 0 }
        }}
      />
      <Helmet>
        <meta name='robots' content='noindex, nofollow' />
        <title>PARTNERS | DARUMA, Inc.</title>
      </Helmet>
      <Container
        maxWidth={false}
        sx={{
          bgcolor: '#3c3c3c'
        }}
      >
        <Link to='/'>
          <Box
            pt={5}
            sx={{
              m: {
                xs: '0 auto 40px',
                sm: '0 0 0 40px'
              },
              width: {
                xs: 54,
                sm: 108
              },
              height: {
                xs: 28,
                sm: 56
              },
              backgroundImage: `url(${Logo})`,
              backgroundPosition: 'center center',
              backgroundRepeat: 'no-repeat',
              backgroundSize: 'contain'
            }}
          />
        </Link>
        <Typography color='#f0f0f0' fontSize={24}>
          担当者紹介
        </Typography>
        <Typography
          variant='h1'
          color='#f0f0f0'
          fontWeight='bold'
          mb={{
            xs: '-14px',
            md: '-28px'
          }}
          fontSize={{
            xs: 50,
            md: 106
          }}
        >
          PARTNER
        </Typography>
        <Box
          position='relative'
          bgcolor='#f0f0f0'
          p='96px 1em'
          borderRadius={4}
        >
          <Box
            position='absolute'
            sx={{
              top: {
                xs: '-75px',
                sm: '-150px'
              },
              right: {
                xs: '30px',
                md: '200px'
              },
              width: {
                xs: '45px',
                sm: '90px'
              },
              height: {
                xs: '123px',
                sm: '246px'
              },
              backgroundImage: `url(${Chara01})`,
              backgroundSize: 'contain',
              backgroundRepeat: 'no-repeat',
              backgroundPosition: 'center center'
            }}
          />
          <Container maxWidth='md'>
            <Box
              sx={{
                margin: '0 auto',
                width: {
                  xs: '100%',
                  sm: '500px'
                }
              }}
            >
              <Typography
                variant='h2'
                align='center'
                mb={4}
                sx={{
                  lineHeight: {
                    xs: '42px',
                    sm: '48px'
                  }
                }}
              >
                私たちプロフェッショナルに、
                <br />
                なんでもご相談ください。
              </Typography>
              <Typography>
                ダルマでは一人一人に、不動産投資のプロフェッショナルがつきます。わからないこと、聞きたいことがあれば気軽にご相談ください。担当者が、お客さまに合わせた最適な提案をさせていただきます。
              </Typography>
            </Box>
            <Grid container spacing={2} mt={8}>
              <Grid item xs={12}>
                <Box
                  sx={{
                    padding: {
                      xs: '20px',
                      sm: '40px'
                    },
                    backgroundColor: '#fff',
                    borderRadius: '8px'
                  }}
                >
                  <Grid container>
                    <Grid item xs={12} sm={12} md={6}>
                      <Box
                        sx={{
                          margin: '0 auto',
                          width: {
                            xs: '240px',
                            sm: '335px'
                          },
                          height: {
                            xs: '200px',
                            sm: '280px'
                          }
                        }}
                      >
                        <img src={memberData.image} alt='Icon' width='100%' />
                      </Box>
                    </Grid>
                    <Grid item xs={12} sm={12} md={6}>
                      <Typography color='#3b4043'>{memberData.role}</Typography>
                      <Typography variant='h2'>{memberData.name}</Typography>
                      <Typography
                        color='#3b4043'
                        sx={{
                          mt: '-6px !important'
                        }}
                      >
                        {memberData.name_en}
                      </Typography>
                      <CustomTable datas={memberData.contacts} />
                    </Grid>
                  </Grid>
                </Box>
              </Grid>
            </Grid>
            <Grid container spacing={2} mt={0}>
              <Grid item xs={12} md={6} display='flex' alignSelf='stretch'>
                <Box
                  width='100%'
                  sx={{
                    padding: {
                      xs: '20px',
                      sm: '40px'
                    },
                    backgroundColor: '#fff',
                    borderRadius: '8px'
                  }}
                >
                  <Typography variant='h2' mb={3}>
                    担当者から一言
                  </Typography>
                  <Typography variant='h3' mb={3}>
                    {memberData.copy}
                  </Typography>
                  <Typography>{memberData.message}</Typography>
                </Box>
              </Grid>
              <Grid item xs={12} md={6} display='flex' alignSelf='stretch'>
                <Box
                  width='100%'
                  sx={{
                    padding: {
                      xs: '20px',
                      sm: '40px'
                    },
                    backgroundColor: '#fff',
                    borderRadius: '8px'
                  }}
                >
                  <Typography variant='h2' mb={3}>
                    基本情報
                  </Typography>
                  <CustomTable datas={memberData.infos} />
                </Box>
              </Grid>
            </Grid>
          </Container>
        </Box>
        <Box
          component='footer'
          display='flex'
          alignItems='center'
          justifyContent='center'
          height={80}
        >
          <Typography color='#fff' fontSize={12}>
            &copy; 2022 DARUMA, Inc.
          </Typography>
        </Box>
      </Container>
    </ThemeProvider>
  );
};

export default MembersIndex;
